:root {
  --font-size-mobile-tablet: 12px;
  --line-height-mobile-tablet: 1.33: --font-size-desktop: 20px;
  --line-height-desktop: 1.2;
  --main-black-color: #000;
  --main-accent-color: #ff6b08;
}

body {
  font-family: Roboto, sans-serif;
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

button {
  cursor: pointer;
  border: none;
  font-family: inherit;
}

a {
  text-decoration: none;
}

img, svg {
  max-width: 100%;
  height: auto;
  display: block;
}

@media screen and (max-width: 767.98px) {
  .gallery {
    flex-direction: column;
    align-items: center;
    gap: 20px;
    display: flex;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279.98px) {
  .gallery {
    flex-flow: wrap;
    gap: 32px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .gallery {
    flex-flow: wrap;
    margin-bottom: -32px;
    margin-right: -16px;
    display: flex;
  }
}

@media screen and (min-width: 768px) and (max-width: 1279.98px) {
  .gallery__item {
    width: calc(50% - 16px);
  }
}

@media screen and (min-width: 1280px) {
  .gallery__item {
    width: 395px;
    margin-bottom: 32px;
    margin-right: 16px;
  }

  .gallery__item:nth-child(3n+3) {
    margin-right: 0;
  }
}

.gallery__img {
  border-radius: 5px;
}

.gallery-text {
  font-size: 12px;
  font-weight: 500;
  line-height: var(--line-height-mobile-tablet);
  margin-top: 8px;
}

@media screen and (min-width: 1280px) {
  .gallery-text {
    font-size: var(--font-size-desktop);
    margin-top: 12px;
    line-height: 1.2;
  }
}

.gallery-text__title {
  text-transform: uppercase;
  color: var(--main-black-color);
}

.gallery-text__info {
  align-items: center;
  display: flex;
}

.gallery-text__genre {
  color: var(--main-accent-color);
}

.gallery-text__rating {
  background-color: var(--main-accent-color);
  color: #fff;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-left: 9px;
  padding: 1px 6px;
  font-size: 12px;
  line-height: 1.16;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

